import { Request } from "@/utils/request";

interface TempateType {
  contents: { name: string; title: string; value: string }[];
  first: string;
  id: number;
  remark: string;
  templateId: string;
  templateTitle: string;
}

interface SendType {
  toUserList: string[];
  templateMessage: TempateType;
}

// 
export const getTemplateList = () => {
  return Request({
    url: "/active-touch/template/list",
  });
};

// 
export const sendTemplateMessage = (data: any) => {
  return Request({
    url: "/active-touch/batch/template/send",
    method: "post",
    data,
  });
};

// 
export const sendOfflineMessage = (data: {
  content: string;
  staffCodeList: string[];
}) => {
  return Request({
    url: "/active-touch/offline/send",
    method: "post",
    data,
  });
};

// 
export const getOfflineStatus = (params: { ids: string }) => {
  return Request({
    url: "/active-touch/offline/send/status",
    params,
  });
};
