import { reactive } from "vue";
const rules = reactive({
  userEmail: [
    { required: true, message: "", tigger: "blur" },
    { type: "email", message: "", tigger: "blur" },
  ],
  userNickName: [{ required: true, message: "", tigger: "blur" }],
  userName: [
    {
      required: true,
      message: "",
      tigger: "blur",
    },
  ],
});

const passForm = reactive({
  oldPassword: "",
  newPassword: "",
  checkPassword: "",
});

const validatePass = (rule, value, callback) => {
  if (value === "") {
    callback(new Error(""));
  } else if (value === passForm.oldPassword) {
    callback(new Error(""));
  }
  callback();
};

const checkPass = (rule, value, callback) => {
  if (value === "") {
    callback(new Error(""));
  } else if (value !== passForm.newPassword) {
    callback(new Error(""));
  }
  callback();
};

const passwordRules = reactive({
  oldPassword: [{ required: true, message: "", tigger: "blur" }],
  newPassword: [
    { validator: validatePass, tigger: "blur" },
    { required: true, message: "", tigger: "blur" },
  ],
  checkPassword: [
    { validator: checkPass, tigger: "blur" },
    { required: true, message: "", tigger: "blur" },
  ],
});

export { passwordRules, passForm, rules };
