import { getCategoryTree, getProductTree } from "@/api/deli/faq";
import { reactive } from "vue";
const categoryType: { lists: any[]; loading: boolean } = reactive({
  lists: [],
  loading: false,
});

const gettreeList = async (id: number): Promise<void> => {
  if (id) {
    categoryType.loading = true;
    const res = await getCategoryTree({
      baseId: id,
    });
    categoryType.loading = false;
    const data: any = res.data;
    categoryType.lists =
      data.categoryList instanceof Array
        ? [
            {
              categoryTitle: "",
              categoryId: "",
              children: [...data.categoryList],
            },
          ]
        : [
            {
              categoryTitle: "",
              categoryId: "",
              children: [],
            },
          ];
  }
};

//  categoryId
const findids = (id, arr: any[] = [], data = categoryType.lists) => {
  const list = [...arr];
  if (id === "" || id === 0) {
    list.unshift("");
    console.log("list", list);
    return list;
  } else {
    let result: any;
    data.forEach((item) => {
      if (item.categoryId === id) {
        list.unshift(id);
        result = findids(item.parentCategoryId, list, categoryType.lists);
      } else {
        if (item.children && item.children.length > 0) {
          result = findids(id, list, item.children);
        }
      }
    });
    if (result) {
      return result;
    }
  }
};

export { gettreeList, categoryType, findids };

