import { Request } from "@/utils/request";

// 
export const submitCommit = (data) => {
  return Request({
    url: "/faq-commit/commit",
    data,
    method: "post",
  });
};

//  
export const getCommitPage = (params) => {
  return Request({
    url: "/faq-commit/page",
    params,
    method: "post",
  });
};

// 
export const getCommitStatus = (params) => {
  return Request({
    url: "/faq-commit/status",
    params,
    method: "post",
  });
};
