import { Request } from "@/utils/request";

// 
export const getRecommendProduct = (params) => {
  return Request({
    url: "/main-screen/recommendation/product/search",
    params,
  });
};

// 
export const getRecommendWords = (params) => {
  return Request({
    url: "/main-screen/recommendation/words",
    params,
  });
};

// 
export const getMainScreen = (params) => {
  return Request({
    url: "/main-screen/inventory",
    params,
  });
};

// 
export const getOrderList = (params) => {
  return Request({
    url: "/main-screen/orders",
    params,
  });
};

// 
export const getUserprofile = (params) => {
  return Request({
    url: "/main-screen/user-profile",
    params,
  });
};

// 
export const getHotTopic = (params) => {
  return Request({
    url: "/main-screen/hot-topic",
    params,
  });
};

// 
export const userSync = (data) => {
  return Request({
    url: "/main-screen/user/sync",
    data,
    method: "post",
  });
};
