import { Request } from "@/utils/request";
import { Service } from "@/gtypes/Service";

type IMStatus = 1 | 2;

// 
export const uploadFile: (params, data) => Promise<Service.Result> = (
  params,
  data
) => {
  return Request({
    url: "/im/http/upload/file",
    params,
    data,
    method: "post",
  });
};

// 
export const setStatus: (params: {
  status: IMStatus;
}) => Promise<Service.Result> = (params) => {
  return Request({
    url: "/im/http/onOffLine",
    method: "post",
    params,
  });
};

// 
export const getHistory: (params: {
  customerName: string;
}) => Promise<Service.Result> = (params) => {
  return Request({
    url: "/im/http/chat/history/v2",
    params,
  });
};

